import React, {Suspense, Component } from 'react';
import { connect } from 'react-redux';
//import {toggle} from './action/actions';
import { BrowserRouter as Router, Switch, Route, Redirect} from 'react-router-dom';

// import BasicLayout from './layout/BasicLayout';
import AppLogin from './layout/login'


function hasLogin(){
		//let user = localStorage.getItem('user');
		let token = 'sdfsdf2sdf2dsf2sd3f'	//localStorage.getItem('token');
		if(token){
			//console.log('in');
			return true;
		}
		else{ //console.log('out');
			return false }
	}
	
const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={ props =>
      hasLogin() ? (<Component {...props} />) : ( <Redirect to={{ pathname: "/login", state: { from: props.location } }} />)
    }
  />
);

class AppRouter extends Component {	
		
  render() {
    return (
			<Suspense fallback={<div>Loading...</div>}>
      	<Router>
					<Switch>				
						<Route exact path='/login' component={AppLogin} />
						<PrivateRoute path='' component={React.lazy(()=>import('./layout/BasicLayout'))} />
					</Switch>
        </Router>
			</Suspense>
    );
  }
}

function mapStateToProps(state) {
  return {
    ActiveUser: state.idtoken,
  };
}
export default connect(mapStateToProps) (AppRouter);