import request from '../services/api'

/*login api*/
export const LOGIN = 'LOGIN';
function loginData(val) {
	return { type: LOGIN, data: val}
}

export function loginAPI(data, model){
	let value = request('/api/admin/login', { method: 'POST', body: data });
	return(dispatch)=>{
		value.then(response => {
			console.log('login api call result', response)
			dispatch(loginData(response))
		})
	}
}

/*FORGOTPASS api*/
export const FORGOTPASS = 'FORGOTPASS';
export function forgotPass(val) {
	return { type: FORGOTPASS, data: val}
}

export function forgotPassword(data){
	let value = request('/api/admin/forgotPassword', { method: 'POST', body: data });
	return(dispatch)=>{
		value.then(response => {
			// if(response.status === 1)
			//   { message.success(response.message); }
			dispatch({ type: FORGOTPASS, data: response})
		})
	}
}

export const DASHBOARD = 'DASHBOARD';
export function dashboard(filter){
	let value = request('/api/admin/dashboard?filter='+filter, { method: 'GET'});
	return (dispatch)=>{
		value.then(response => {
			dispatch({ type: DASHBOARD, data: response});
		});
	}
}

export const NOTIFICATION = 'NOTIFICATION';
export function notifications(page,limit){
	let value = request('/api/admin/notifications?limit=50&page=1', { method: 'GET'});
	return (dispatch)=>{
		value.then(response => {
			dispatch({ type: NOTIFICATION, data: response});
		});
	}
}