import { NOTIFICATION, DASHBOARD } from '../actions'


let item = {};
export function Global(state = item,action) {
	switch (action.type) {
		case NOTIFICATION:
		  return{
          ...state,
          data:action.data,
          status:action.data.status
			}
		case DASHBOARD:
			return{
          ...state,
					dashboard:{	
						data:action.data.data,
						status:action.data.status
					}
			}
		default:
		  return{
				...state
			}
		}
}